import fastify, { FastifyInstance, FastifyLoggerOptions } from 'fastify';
import cors from '@fastify/cors';
import helmet from '@fastify/helmet';
import healthcheck from './services/healthcheck';

export const getApplication = (): FastifyInstance => {
  const app = fastify({
    logger: {level: 'debug'} as FastifyLoggerOptions,
  });

  app.register(helmet);
  app.register(cors, {
    origin: '*',
  });

  app.register(
    //routes registration
    function (_fastify, _, done) {
      app.register(healthcheck);
      done();
    },
    { prefix: '/v1' },
  );

  return app;
};